body {
  background-color: #1E1E1E;
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #ffffff;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.header {
  display: flex;
  justify-content: center;
  margin: 2rem;
  font-size: 2.4;
  font-weight: 100;
  font-style: italic;
}
@media (min-width: 768px) {
  .header {
    justify-content: end;
  }
}
.header a {
  margin: 0 1rem;
  color: #ffffff;
}
.header a:hover {
  border-bottom: 1px solid #ffffff;
}
.header span {
  color: #64FFDA;
}

.heading {
  font-size: 7rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  font-weight: 100;
  font-style: italic;
  color: #64FFDA;
  margin-top: 8rem;
}
@media (min-width: 768px) {
  .heading {
    font-size: 15rem;
  }
}

.container {
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 9fr;
  width: 31.15rem;
  height: 40.3rem;
  background-color: rgba(0, 0, 0, 0.25);
  justify-content: center;
  border-radius: 1.6rem;
  font-size: 1.5rem;
  font-weight: 100;
}
@media (min-width: 768px) {
  .container {
    width: 62.3rem;
    height: 28.6rem;
    font-size: 3rem;
  }
}

.text-head {
  margin: 2rem;
  margin-top: 8rem;
  font-size: 2rem;
  text-align: center;
  position: absolute;
  max-width: 27.15rem;
  margin-left: 3rem;
}
@media (min-width: 768px) {
  .text-head {
    max-width: 54.3rem;
  }
}

.quote {
  position: relative;
  background-position: left;
  margin: 2rem;
  margin-right: 0;
  background-image: url(/build/img/comilla.svg);
  background-repeat: no-repeat;
  width: 22.5px;
  height: 20.5px;
}
@media (min-width: 768px) {
  .quote {
    width: 55px;
    height: 41px;
  }
}

.btn-green {
  margin: 0 auto;
  padding: 2rem;
  color: #64FFDA;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 64px;
  font-size: 1.4rem;
  margin-top: 3rem;
  border: 1px solid #64FFDA;
}
@media (min-width: 768px) {
  .btn-green {
    max-width: 128px;
  }
}
.btn-green:hover {
  -webkit-box-shadow: -6px 9px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -6px 9px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: -6px 9px 5px 0px rgba(0, 0, 0, 0.75);
}

.aboutme {
  width: 44.15rem;
  height: 45.3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 3rem;
  color: #ffffff;
  font-weight: 100;
  margin: 0 auto;
  margin-top: 20rem;
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 1.6rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .aboutme {
    width: 88.3rem;
    height: 62.6rem;
    font-size: 3rem;
    display: grid;
    grid-template-columns: 3fr 1fr;
  }
}
.aboutme span {
  color: #64FFDA;
}
.aboutme .text-about {
  max-width: 30.3rem;
}
.aboutme .image-about {
  margin: 2rem;
  width: 17.5rem;
  height: 23.25rem;
  display: none;
  background-repeat: no-repeat;
  background-position: center;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background: linear-gradient(to top, rgba(4, 253, 203, 0.368627451), rgba(7, 250, 128, 0.4745098039)), url(/build/img/aboutme.jpeg) no-repeat center;
  background-size: cover;
}
@media (min-width: 768px) {
  .aboutme .image-about {
    width: 29.5rem;
    height: 46.5rem;
    display: flex;
  }
}
.aboutme p {
  max-width: 30.3rem;
  font-size: 2rem;
}

.microhead {
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  .microhead {
    font-size: 3rem;
  }
}

.experience {
  width: 44.15rem;
  height: 45.3rem;
  font-size: 2rem;
  color: #ffffff;
  font-weight: 100;
  margin: 0 auto;
  margin-top: 20rem;
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 1.6rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .experience {
    width: 88.3rem;
    height: 62.6rem;
    font-size: 3rem;
  }
}
.experience .green, .experience span {
  color: #64FFDA;
  margin-right: 1rem;
}

.sidenav {
  width: 3.85rem;
  height: 15.2rem;
  border-radius: 1.6rem;
  background-color: rgba(0, 0, 0, 0.25);
  position: fixed;
  margin-left: 2rem;
  z-index: 1;
  overflow: hidden;
  margin-top: 18rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .sidenav {
    width: 7.7rem;
    height: 29.7rem;
  }
}
.sidenav .social {
  color: #64FFDA;
  font-size: 24px;
  margin: 1rem;
}
@media (min-width: 768px) {
  .sidenav .social {
    font-size: 48px;
  }
}

.projects {
  display: none;
  margin-top: 20rem;
  color: #ffffff;
  font-size: 3rem;
  font-weight: 100;
}
@media (min-width: 768px) {
  .projects {
    display: block;
  }
}

.heading-projects {
  color: #ffffff;
  font-weight: 100;
  font-style: normal;
}
.heading-projects span {
  color: #64FFDA;
}

#carousel-wrapper {
  width: auto;
  height: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
#carousel-wrapper #menu {
  height: 51.6rem;
  width: 88.3rem;
  overflow: hidden;
  font-weight: 100;
  line-height: 1;
  display: none;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  transition: all 0.6s ease-in-out;
}
@media (min-width: 768px) {
  #carousel-wrapper #menu {
    display: flex;
  }
}
#carousel-wrapper #menu #current-option {
  position: relative;
  width: 100%;
  height: 100%;
  transform: translate(-25%, 0%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
#carousel-wrapper #menu #current-option #current-option-text1 {
  font-size: 3rem;
  line-height: 3rem;
  width: 220px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
#carousel-wrapper #menu #current-option #current-option-text1::before {
  content: attr(data-next-text);
  position: absolute;
  transform: translate(0%, 380px);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
#carousel-wrapper #menu #current-option #current-option-text1::after {
  content: attr(data-previous-text);
  position: absolute;
  transform: translate(0%, -380px);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
#carousel-wrapper #menu #current-option #current-option-text2 {
  font-size: 2rem;
  width: 220px;
  height: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#carousel-wrapper #menu #current-option #current-option-text2::before {
  content: attr(data-next-text);
  position: absolute;
  transform: translate(0%, 380px);
  width: 100%;
  height: 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}
#carousel-wrapper #menu #current-option #current-option-text2::after {
  content: attr(data-previous-text);
  position: absolute;
  transform: translate(0%, -380px);
  width: 100%;
  height: 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}
#carousel-wrapper #menu #previous-option {
  width: 1.5rem;
  height: 1.5rem;
  border: none;
  display: block;
  cursor: pointer;
  background: url("data:image/svg+xml,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 256 256'%3E%3Cpolygon points='225.813,48.907 128,146.72 30.187,48.907 0,79.093 128,207.093 256,79.093' fill='%23333'%3E%3C/polygon%3E%3C/svg%3E");
  background-size: cover;
  position: absolute;
  transform: translate(310px, 50px);
}
#carousel-wrapper #menu #next-option {
  width: 1.5rem;
  height: 1.5rem;
  border: none;
  display: block;
  cursor: pointer;
  background: url("data:image/svg+xml,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 256 256'%3E%3Cpolygon points='225.813,48.907 128,146.72 30.187,48.907 0,79.093 128,207.093 256,79.093' fill='%23333'%3E%3C/polygon%3E%3C/svg%3E");
  background-size: cover;
  position: absolute;
  transform: translate(310px, -50px) rotate(180deg);
}
#carousel-wrapper #image {
  height: 240px;
  width: 240px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 101;
  position: absolute;
  transform: translate(140px, 0);
}
#carousel-wrapper.anim-next {
  pointer-events: none;
}
#carousel-wrapper.anim-next #current-option-text1 {
  animation: next-text 0.65s 0.085s;
}
#carousel-wrapper.anim-next #current-option-text2 {
  animation: next-text 0.65s 0.085s;
}
#carousel-wrapper.anim-next #previous-option {
  animation: next-top-arrow 0.65s 0.085s;
}
#carousel-wrapper.anim-next #next-option {
  animation: next-bottom-arrow 0.65s 0.085s;
}
#carousel-wrapper.anim-next #image {
  animation: next-image 0.65s 0.085s;
}
#carousel-wrapper.anim-previous {
  pointer-events: none;
}
#carousel-wrapper.anim-previous #current-option-text1 {
  animation: previous-text 0.65s 0.085s;
}
#carousel-wrapper.anim-previous #current-option-text2 {
  animation: previous-text 0.65s 0.085s;
}
#carousel-wrapper.anim-previous #previous-option {
  animation: previous-top-arrow 0.65s 0.085s;
}
#carousel-wrapper.anim-previous #next-option {
  animation: previous-bottom-arrow 0.65s 0.085s;
}
#carousel-wrapper.anim-previous #image {
  animation: previous-image 0.65s 0.085s;
}

@keyframes previous-text {
  50%, 55% {
    transform: translate(0%, 390px);
  }
  to {
    transform: translate(0%, 380px);
  }
}
@keyframes previous-top-arrow {
  50% {
    transform: translate(310px, 53px);
  }
}
@keyframes previous-bottom-arrow {
  50% {
    transform: translate(310px, -47px) rotate(180deg);
  }
}
@keyframes previous-image {
  0% {
    transform: translate(140px, 0) scale(1);
    opacity: 1;
  }
  70% {
    transform: translate(140px, 0) scale(1.1);
    opacity: 0;
  }
  100% {
    transform: translate(140px, 0) scale(1);
    opacity: 1;
  }
}
@keyframes next-text {
  50%, 55% {
    transform: translate(0%, -390px);
  }
  to {
    transform: translate(0%, -380px);
  }
}
@keyframes next-top-arrow {
  50% {
    transform: translate(310px, 47px);
  }
}
@keyframes next-bottom-arrow {
  50% {
    transform: translate(310px, -53px) rotate(180deg);
  }
}
@keyframes next-image {
  0% {
    transform: translate(140px, 0) scale(1);
    opacity: 1;
  }
  70% {
    transform: translate(140px, 0) scale(1.1);
    opacity: 0;
  }
  100% {
    transform: translate(140px, 0) scale(1);
    opacity: 1;
  }
}
