.sidenav {
    @include tablet {
        width: 7.7rem;
        height: 29.7rem;
    }
    width: 3.85rem;
    height: 15.2rem;

    border-radius: 1.6rem;
    background-color: $terciario;
    position: fixed;
    margin-left: 2rem;
    z-index: 1;
    overflow: hidden;
    margin-top: 18rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .social {
        color: $primario;
        font-size: 24px;
        margin: 1rem;
        @include tablet {
            font-size: 48px;
        }
    }
}
