.contact {

    margin-top: 18rem;
    margin-bottom: 18rem;
    font-weight: 100;
    font-size: 3rem;
    color: $blanco;
    @include tablet {
        width: 88.3rem;
    }
    max-width: 30.3rem;
    margin: 18rem auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    fieldset {
        border-color: $primario;
        legend {
            color: $primario;
        }
        .input {
            margin: 2rem;
            display: flex;
            justify-content: center;
            .input-field {
                margin-left: 2rem;
                @include tablet {
                    max-width: 100%;
                }
                max-width: 20rem;
            }
        }
        input, textarea {
            border-color: $primario;
            background: none;
        }

        .max {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        textarea {
            @include tablet {
                width: 88.3rem;
            }
            width:30.3rem;
            height: 20rem;
        }
    }
    
}

.btn-submit {
    color: $primario;
    padding: 1rem;
    text-align: center;
    display: block;
    margin: 2rem auto;
    margin-bottom: .5rem;
    &:hover {
        -webkit-box-shadow: -6px 9px 5px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: -6px 9px 5px 0px rgba(0,0,0,0.75);
        box-shadow: -6px 9px 5px 0px rgba(0,0,0,0.75);
    }
}
h2 {
    font-weight: 700;
    color: $primario;
    font-family: $fuente_principal;
    letter-spacing: 4px;
    font-style: italic;
}