.header {
    display: flex;
    @include tablet {
        justify-content: end;
    }
    justify-content: center;
    margin: 2rem;
    font-size: 2.4;
    font-weight: 100;
    font-style: italic;
    a {
        margin: 0 1rem;
        color: $blanco;
        &:hover {
            border-bottom: 1px solid $blanco;
        }
    }
    span {
        color: $primario;

    }
}

.heading {
    @include tablet {
        font-size: 15rem;
    }
    font-size: 7rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-transform: uppercase;
    font-weight: 100;
    font-style: italic;
    color: $primario;
    margin-top: 8rem;
}

.container {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 9fr;
    @include tablet {
        width: 62.3rem;
        height: 28.6rem;
        font-size: 3rem;
    }
    width: 31.15rem;
    height: 40.3rem;
    background-color: $terciario;
    justify-content: center;
    border-radius: 1.6rem;
    font-size: 1.5rem;
    font-weight: 100;
    
}
.text-head {
    @include tablet {
        max-width: 54.3rem;
    }
    margin: 2rem;
    margin-top: 8rem;
    font-size: 2rem;
    text-align: center;
    position: absolute;
    max-width: 27.15rem;
    margin-left: 3rem;
    
}
.quote {
    position: relative;
    background-position: left;
    margin: 2rem;
    margin-right: 0;
    background-image: url(/build/img/comilla.svg);
    background-repeat: no-repeat;
    @include tablet {
        width: 55px;
        height: 41px;
    }
    width: 22.5px;
    height: 20.5px;
}

.btn-green {
    margin: 0 auto;
    padding:2rem;
    color: $primario;
    display: flex;
    text-align: center;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include tablet {
        max-width: 128px;
    }
    max-width: 64px;
    font-size: 1.4rem;
    margin-top: 3rem;
    border:1px solid $primario;
    &:hover {
        -webkit-box-shadow: -6px 9px 5px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: -6px 9px 5px 0px rgba(0,0,0,0.75);
        box-shadow: -6px 9px 5px 0px rgba(0,0,0,0.75);
    }
}