.aboutme {
    @include tablet {
        width: 88.3rem;
        height:62.6rem;
        font-size: 3rem;
        display: grid;
        grid-template-columns: 3fr 1fr;

    }
    width: 44.15rem;
    height:45.3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 3rem;
    color: $blanco;
    font-weight: 100;
    margin: 0 auto;
    margin-top: 20rem;
    background-color: $terciario;
    border-radius: 1.6rem;
    padding: 2rem;
    span {
        color: $primario;
    }
    .text-about {
        max-width: 30.3rem;
    }
    .image-about {
        margin: 2rem;
        @include tablet{
            width: 29.5rem;
            height: 46.5rem;
            display: flex;
        }
        width: 17.5rem;
        height: 23.25rem;
        
        display: none;
        background-repeat: no-repeat;
        background-position: center;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        background: linear-gradient(to top, #04fdcb5e, #07fa8079), url(/build/img/aboutme.jpeg) no-repeat center;
        background-size: cover;
        
        
        
    }


    p {
        max-width: 30.3rem;
        font-size: 2rem;
    }
}

.microhead {
    font-size: 2.4rem;
    @include tablet {
        font-size: 3rem;
    }
}